.header {

  &__menu {
    background: var(--background);
    inline-size: 100%;
    position:absolute;
    margin-block-start: 30px;
    margin-inline-start: initial;
    transition: 0.3s;
    z-index: 20;

    &--hidden {
      margin-inline-start: -100%;
      visibility: hidden;
    }

    &-list {
      list-style: none;
      padding: 0;
      margin:0;
      text-align: center;
    }
  
    &-item {
      padding: 0 1rem;
    }

    &-link {
      @include link-text;
    }

    &-button {
      cursor: pointer;
      background-color: transparent;
      border: 0;
    }

    &-button[aria-expanded='true'] {
      .octicon-three-bars {
        display:none;
      }
    }

    &-button[aria-expanded='false'] {
      .octicon-x {
        display:none;
      }
    }
  }
}

.footer {

  &__menu-list {
    display: flex;
    list-style: none;
    padding: 0;
  }

  &__menu-item {
    padding-inline: 1em;

    &:first-of-type {
      padding-inline-start: 0;
    }

    &:last-of-type {
      padding-inline-end: 0;
    }
  }

  &__menu-link {
    @include link-text;
  }
}